@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
.pageNumbers {
  list-style: none;
  display: flex;
}

.pageNumbers li {
  padding: 10px;
  border: 1px solid rgb(0, 0, 0);
  cursor: pointer;
}

.pageNumbers li.active {
  background-color: rgb(108, 76, 250);
  color: black;
}

.pageNumbers li button {
  background-color: transparent;
  border: none;
  color: rgb(218, 0, 0);
  font-size: 1.5rem;
  cursor: pointer;
}
.pageNumbers li button:hover {
  background-color: rgb(102, 91, 197);
  color: black;
}
.pageNumbers li button:focus {
  outline: none;
}

.loadmore {
  background-color: #0bb7a7;
  color: #fff;
  font: 490 0.89vw Montserrat;
  border: 1px solid #0bb7a7;
  border-radius: 5px;
  box-shadow: 0px 3px 6px #00000029;
  height: 2.3vw;
  width: 108px;
  cursor: pointer;
}

:root {
  font-size: 14px;
}

.styled-table {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 1.3rem;
  font-family: sans-serif;
  min-width: 400px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.styled-table thead tr {
  background-color: #009879;
  color: #ffffff;
  text-align: left;
}

.styled-table th,
.styled-table td {
  padding: 12px 15px;
}

.styled-table tbody tr {
  border-bottom: 1px solid #dddddd;
}

.styled-table tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

.styled-table tbody tr:last-of-type {
  border-bottom: 2px solid #009879;
}

.styled-table tbody tr.active-row {
  font-weight: bold;
  color: #0c1413;
  background-color: #51c4b0;
}
.styled-table tbody tr:hover td {
  background-color: #46c075;
  cursor: pointer;
}

body {
  background: #f2f2f2;
  font-family: "Open Sans", sans-serif;
}

.search {
  width: 100%;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 3px solid #00b4cc;
  border-right: none;
  padding: 5px;
  height: 33px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9dbfaf;
}

.searchTerm:focus {
  color: #00b4cc;
}

.searchButton {
  width: 40px;
  height: 49px;
  border: 1px solid #00b4cc;
  background: #00b4cc;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

/*Resize the wrap to see the search bar change!*/
.wrap {
  width: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

